import React, { useContext } from 'react';
import styled from 'styled-components'

import GameContext from './../../game-context'


const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 80px;

    .instruction {
        margin-bottom: 20px;
    }
    
    span,
    form, 
    label {
        display: inline-flex;
        align-items: center;
    }
    
    span,
    label {
        font-family: "LoRes12";
        font-size: 14px;
    }
    
    form{
        margin-bottom: 15px;
    }

    span {
        min-width: 170px;
    }
    
    label {
        min-width: 80px;
    }
`

const Text = styled.p`
    font-family: "LoRes22";
    font-size: 24px;
    display: block;
    width: 100%;
    margin-bottom: 20px;
`

export default function MainOptions(props) {

    const context = useContext(GameContext).state;

    function handleOptionChange(event, option, label) {

        function process(option, label, value, context, extra = {}) {
            props.handleChangeState({
                [option]: {
                    ...context.gameOptions,
                    ...extra,
                    [label]: value
                }
            })
            let checks = document.getElementsByName(label)
            checks.forEach((item) => {
                if (item !== event.target.name) item.checked = false
            })
        }

        function de_activateCheck(state) {
            let checks = document.getElementsByName('musicAmbience')
            checks.forEach((item) => {
                item.disabled = state
            })
        }

        function updateCanvas(value) {
            //TODO: replace with context
            //AppGlobal.canvas.setState({ isFull: value })
        }

        let value = event.target.value === 'true' ? true : event.target.value === 'false' ? false : event.target.value

        if (label === 'fullScreen') {
            updateCanvas(value)
            process(option, label, value, context)
        } else if (label === 'sound') {
            if (value === false) {
                process(option, label, value, context, { musicAmbience: false })
                de_activateCheck(true)
            } else {
                process(option, label, value, context)
                de_activateCheck(false)
            }
        } else {
            process(option, label, value, context)
        }
    }

    return (
        <Wrapper>
            <Text>OPTIONS</Text>
            <span className="instruction">click to change options</span>
            <form>
                <span>Weather</span>
                <label>
                    <input
                        type="checkbox" name="weather" value={'celcius'} checked={context.gameOptions.weather === 'celcius' ? true : false}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'weather')} /> Celcius </label>
                <label>
                    <input
                        type="checkbox" name="weather" value={'farenheit'} checked={context.gameOptions.weather === 'farenheit' ? true : false}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'weather')} /> Farenheit </label>
            </form>
            <form>
                <span>Sound</span>
                <label>
                    <input
                        type="checkbox" name="sound" value={true} checked={context.gameOptions.sound}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'sound')} /> On </label>
                <label>
                    <input
                        type="checkbox" name="sound" value={false} checked={!context.gameOptions.sound}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'sound')} /> Off </label>
            </form>
            <form>
                <span>Music / Ambience</span>
                <label>
                    <input
                        type="checkbox" name="musicAmbience" value={true} checked={context.gameOptions.musicAmbience}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'musicAmbience')} /> On </label>
                <label>
                    <input
                        type="checkbox" name="musicAmbience" value={false} checked={!context.gameOptions.musicAmbience}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'musicAmbience')} /> Off </label>
            </form>
            <form>
                <span>Full Screen</span>
                <label>
                    <input
                        type="checkbox" name="fullScreen" value={true} checked={context.gameOptions.fullScreen === true ? true : false}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'fullScreen')} /> On </label>
                <label>
                    <input
                        type="checkbox" name="fullScreen" value={false} checked={context.gameOptions.fullScreen === false ? true : false}
                        onChange={(event) => handleOptionChange(event, 'gameOptions', 'fullScreen')} /> Off </label>
            </form>
        </Wrapper>
    )
}