import React from 'react';
import styled from 'styled-components'

import BottomButtons from './../BottomButtons.js'
import SquareButtonsBar from './../SquareButtonsBar.js'
import { career, challenge, load } from './../../styles/icons.js'
import { greenButton, greenButtonBorder, dark, clear } from './../../styles/variables.js'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0 !important;
`

const ButtonsWrapper = styled.div`
    display: flex;
    width: 100%;
    padding: 36px 0 19px;

    div {
        margin-right: 4px;
    }
`

const Text = styled.span`
    font-family: "LoRes12";
    font-size: 12px;
    display: block;
    width: 100%;
`

const DataSlots = styled.div`
    padding-top: 17px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: ${props => props.theme.gameState ? '56px' : '80px'};

`

const DataColumn = styled.div`
    display: flex;
    flex-direction: column;
`

const Slot = styled.label`
    display: inline-flex;
    align-items: center;
    font-family: "LoRes12";
    background: ${greenButton};
    border-radius: 4px;
    border-right: 1px solid ${dark};
    border-bottom: 1px solid ${dark};
    border-top: 1px solid ${greenButtonBorder};
    border-left: 1px solid ${greenButtonBorder};
    box-shadow: 1px 1px rgba(0,0,0,0.6);
    cursor: pointer;
    margin: 2px 2px;
    padding: 6px 16px 7px;
    transition: opacity 0.2s ease;
    transition: all 0.2s ease;

    span,input {
        cursor: pointer;
    }

    &:hover:not([active=true],[disabled=true]) {
        opacity: 0.92;
    }

    input[type="text"] {
        width: 110px;
        background: transparent;
        color: ${clear};
        border: none;

           color: ${clear};
        }
    }

    &[disabled="true"] {
        background: transparent;
        opacity: 0.8;
    }
`

export default function MainPlay(props) {
    const [state, setState] = React.useState({
        isSelecting: false,
        isLoading: false
    })

    React.useEffect(() => {
        let slots = document.querySelectorAll('.Slot');
        slots.forEach((item) => {
            item.querySelector('input').value = 'Empty'
        })
    }, []);

    const handleLoading = () => {
        setState({ isLoading: true })
        document.querySelectorAll('.Slot').forEach((item) => {
            item.querySelector('input').setAttribute('disabled', true)
            item.querySelector('input').value = 'Empty'
            item.setAttribute('disabled', true)
        })
    }

    const handleSlotSelected = (e) => {
        setState({ isSelecting: true })
        e.target.parentNode.setAttribute('active', true)
        e.target.value = ''

        document.querySelectorAll('.Slot').forEach((item) => {
            if (typeof item.attributes.active === 'undefined') {
                item.querySelector('input').setAttribute('disabled', true)
                item.setAttribute('disabled', true)
            }
        })
    }

    const handleCancel = () => {
        setState({ isSelecting: false })
        setState({ isLoading: false })

        document.querySelectorAll('.Slot').forEach((item) => {
            item.removeAttribute('disabled')
            item.removeAttribute('active')
            item.querySelector('input').removeAttribute('disabled')
            item.querySelector('input').value = 'Empty'
        })
    }

    const handleStart = () => {
        props.handleChangeState({ gameLayout: 'Prepare Menu' })
        props.handleChangeState({ topLeftContainer: 'results' })
        props.handleChangeState({ LeftColumn: 'Profit & Lost' })
    }

    return (
        <Wrapper>
            <Text>Select a game mode or load a game in progress</Text>
            <SquareButtonsBar Wrapper={ButtonsWrapper} buttons={[
                {
                    handleClick: handleCancel,
                    handleChangeState: props.handleChangeState,
                    buttonDimentions: { height: '49px', width: '72px' },
                    screen: 'LeftColumn',
                    title: 'new career',
                    img: career,
                    imgWidth: "15px",
                },
                {
                    handleClick: handleCancel,
                    handleChangeState: props.handleChangeState,
                    buttonDimentions: { height: '49px', width: '72px' },
                    screen: 'LeftColumn',
                    title: 'new challenge',
                    img: challenge,
                    imgWidth: "23px",
                },
                {
                    handleClick: handleLoading,
                    handleChangeState: props.handleChangeState,
                    buttonDimentions: { height: '49px', width: '57px' },
                    screen: 'LeftColumn',
                    title: 'load game',
                    img: load,
                    imgWidth: "23px",
                },
            ]} />
            <Text>Select a game slot to save your progress</Text>
            <DataSlots theme={{ gameState: state.isSelecting }}>
                <DataColumn>
                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>1</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>2</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>3</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>4</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                </DataColumn>
                <DataColumn>
                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>5</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>6</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>7</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                    <Slot className="Slot" theme={{ isSelecting: state.isSelecting, isLoading: state.isLoading }}>
                        <span>8</span> &nbsp;
                        <input tabIndex={-1} onFocus={handleSlotSelected} type="text" />
                    </Slot>

                </DataColumn>
            </DataSlots>
            <BottomButtons gameState={state.isSelecting}>
                <button onClick={handleCancel}>CANCEL</button>
                <button onClick={handleStart}>OK</button>
            </BottomButtons>
        </Wrapper >
    )
}
