import React, { useContext } from 'react';
import GameContext from './../game-context'

import MainButtonBar from './left-tops/MainButtonBar.js'
import PlayButtonBar from './left-tops/PlayButtonBar.js'

export default function TopLeftContainer(props) {
    const context = useContext(GameContext);

    if (context.state.gameLayout === 'Main Menu') {
        return (
            <MainButtonBar handleChangeState={props.handleChangeState} />
        )
    } else if (context.state.gameLayout === 'Prepare Menu') {
        return (
            <PlayButtonBar handleChangeState={props.handleChangeState} />
        )
    } else {
        return ' Not valid to TopLeftContainer'
    }
}


