import React, { useState, useContext } from 'react';
import styled from 'styled-components'

import GameContext from './../game-context'

import { greenButton, greenButtonBorder, greenButtonActive, dark } from './../styles/variables.js'

const Wrapper = styled.div`
    display: flex;
    cursor: pointer;
    border-radius: 4px;
    width: ${props => props.theme.width ? props.theme.width : 'fit-content'};
    height: ${props => props.theme.height ? props.theme.height : 'auto'};
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    border-right: 1px solid ${dark};
    border-bottom: 1px solid ${dark};
    border-top: 1px solid ${greenButtonBorder};
    border-left: 1px solid ${greenButtonBorder};
    box-shadow: ${props => props.theme.isSelected ? '0px 0px rgba(0,0,0,0.6)' : '1px 1px rgba(0,0,0,0.6)'};
    background: ${props => props.theme.isSelected ? greenButtonActive : greenButton};
    transition: opacity 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;

    p, img {
        cursor: pointer;
        user-drag: none;
        -webkit-user-drag: none;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
    }
    
    p {
        font-family: "pixelade";
        font-size: 14px;
    }
    
    &:hover{
        opacity: 0.92;
    }
    &:active{
        box-shadow: 0px 0px rgba(0,0,0,0.6);
        background: ${greenButtonActive};
    }
`


export default function SquareButton(props) {
    
    const [state, updateTheState] = useState({ isSelected: false })
    
    const height = props.dimentions && props.dimentions.height ? props.dimentions.height : false
    const width = props.dimentions && props.dimentions.width ? props.dimentions.width : false
    const title = props.title
    const screen = props.screen
    const _state = useContext(GameContext).state[screen]
    
    function buttonHandler(screen, title) {
        if (title !== 'quit') {
            updateTheState({ isSelected: state.isSelected === true ? false : true });
            if(typeof props.handleClick !== 'undefined') {
                props.handleClick(true)
            }
            props.handleChangeState({ [screen]: title })
        } else {
            //TODO: replace with context
            //AppGlobal.mountedGame.ejectGame()
        }
    }


    return (
        <Wrapper
            onClick={()=>buttonHandler(screen, title)}
            theme={{ height: height, width: width, isSelected: title === _state ? true : false }}>
            {props.children}
            <p>{title}</p>
        </Wrapper>
    )

}