import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route, Link, Redirect } from "react-router-dom";
import AppContext from './app-context.js'
import Bicho from './bicho/Bicho.js'
import AppMain from './pages/AppMain'
import RouterControls from './modules/RouterControls'
import LemonadeTycoon from './games/Lemonade_Tycoon/LemonadeTycoon'




export default function App() {
  const [isPlaying, setState] = useState(false)
  const options = [
    {
      handlerValue: true,
      to: "/lemonade_tycoon",
      name: 'Lemonade Tycoon'
    },
  ]
  return (
    <Router>
      <AppContext.Provider value={{ bicho: Bicho.init({ LemonadeTycoon: new LemonadeTycoon() }), isPlaying: isPlaying }}>
        
        {/* Template:
          <Route path="/lemonade_tycoon">
            {isPlaying ? <LemonadeTycoon /> : <Redirect to="/" />}
          </Route>
        */}
        <Switch>
          <Route path="/lemonade_tycoon">
            {isPlaying ? <LemonadeTycoon /> : <Redirect to="/" />}
          </Route>
          <Route path="/">
            <AppMain />
          </Route>
        </Switch>

        <RouterControls
          Link={Link}
          isPlaying={isPlaying}
          setState={setState}
          options={options} />
      </AppContext.Provider>
    </Router>
  )
}
