import React from 'react';
import styled from 'styled-components'

import GameContext from './../game-context.js'
import TopLeftContainer from './TopLeftContainer.js'
import LeftColumn from './LeftColumn.js'
import RightColumn from './RightColumn.js'
import BottomButtons from './BottomButtons.js'
import { stripedBackground, breakColumns } from '../styles/variables.js'

const TopRightContainer = styled.div``

const Wrapper = styled.div`
    display: block;
    position: relative;
    z-index: 1;
    background-image: url(${stripedBackground});
    background-repeat: repeat;
    height: 100%;
    width: 100%;
    min-width: 480px;
    padding: 0 8px;
`

const GridContainer = styled.div`
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, max(350px, calc(50% - 4px)));
    grid-column-gap: 8px;

    @media(${breakColumns}) {
        grid-template-columns: 1fr;
    }
`

const StatsBar = styled.div``
const Style = { position: 'absolute', bottom: '0', width: 'calc(50% - 22px)' }

export default function Layout(props) {
    const context = React.useContext(GameContext);

    const handleMain = () => {
        props.handleChangeState({ LeftColumn: 'new career', gameLayout: 'Main Menu', topLeftContainer: 'play!' })
        console.log('handleMain')
    }
    const handleStart = () => {
        console.log('handleStart')
    }
    return (
        <Wrapper>
            <StatsBar>asdasd</StatsBar>
            <GridContainer>
                <TopLeftContainer handleChangeState={props.handleChangeState} />
                <TopRightContainer handleChangeState={props.handleChangeState} />
            </GridContainer>
            <GridContainer>
                <div>
                    <LeftColumn handleChangeState={props.handleChangeState} />
                    <BottomButtons Style={Style} gameState={context.state.gameLayout === 'Prepare Menu' ? true : false}>
                        <button onClick={handleMain}>MAIN MENU</button>
                        <button onClick={handleStart}>START DAY</button>
                    </BottomButtons>
                </div>
                <RightColumn handleChangeState={props.handleChangeState} />
            </GridContainer>
        </Wrapper>
    )
}