import React from 'react';
import styled, { css } from "styled-components";
import { clear } from './../styles/variables'

const Wrapper = styled.div`
    height: 100%;
    display: ${props => (props.isPlaying) ? 'flex;' : 'block;'}
    padding: ${props => (props.isPlaying) ? '0;' : '40px 20px;'}
    width: 100%;

    a {
        background-color: green;    
        padding: 20px;
        opacity: 1;
        cursor: pointer;
        transition: opacity ease 0.2s;
        color: ${clear};
        text-decoration: none;

        &:hover {
            opacity: 0.8;
        }
    }
    ${props => (props.isPlaying) ? css`
        flex-direction: column;
        justify-content: space-between;
    ` : ''}
`

export default function RouterControls({ isPlaying, setState, Link, options }) {
    const output = []

    const controlsGenerator = (options) => {
        options.forEach((value) => {
            output.push(
                <Link key={value.name}
                    style={{ display: !isPlaying ? 'inline-block' : 'none' }}
                    onClick={() => setState(value.handlerValue)}
                    to={value.to}>{value.name}</Link>
            )
        })
        return output
    }

    return (
        <Wrapper isPlaying={isPlaying}>
            {controlsGenerator(options)}
        </Wrapper>
    )
}