import React from 'react';

import SquareButton from './SquareButton.js' //TODO: delete

const SquareButtonsBar = ({ Wrapper, buttons }) => {

    const handleGeneration = (buttons) => {
        let output = []
        buttons.forEach((value, index, array) => {
            output.push(
                <SquareButton
                    key={value.title}
                    handleClick={value.handleClick}
                    handleChangeState={value.handleChangeState}
                    dimentions={value.buttonDimentions}
                    screen={value.screen}
                    title={value.title}>
                    <img src={value.img} alt={value.title} width={value.imgWidth} style={{ marginBottom: `${value.imgBottomFix ? value.imgBottomFix : 0}px` }} />
                </SquareButton>
            )
        })
        return output
    }

    return (
        <Wrapper>
            {handleGeneration(buttons)}
        </Wrapper>
    )
}

export default SquareButtonsBar