import React from 'react';
import styled from 'styled-components'

import GameContext from './../game-context'

import { breakColumns, greenRightColumn } from './../styles/variables.js'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 8px 8px 90px;
    background: ${greenRightColumn};
    border-radius: 8px;
    box-shadow: inset 3px 3px rgba(0,0,0,0.6);
    
    @media(${breakColumns}) {
        padding: 8px 8px 8px;
    }
`
export default function RightColumn(props) {
    const context = React.useContext(GameContext);

    if (context.state.gameLayout === 'Main Menu') {
        return (
            <Wrapper>'RIGHT COLUMN'</Wrapper>
        )
    } else {
        return (
            <Wrapper>'RIGHT COLUMN'</Wrapper>
        )
    }
}

