import React, { useState, useContext } from 'react';
import styled from 'styled-components'
import GameContext from './../../game-context'


const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 400px;
`

const Controls = styled.div`
    text-align: center;
    margin-top: auto;
    margin-bottom: 0;
    
    button {
        margin: 0 4px;
    }
`

const Title = styled.span`
    font-family: "LoRes22";
    font-size: 22px;
    display: block;
    width: 100%;
    margin-bottom: 8px;
`

const SubTitle = styled.span`
    font-family: "LoRes12";
    font-size: 12px;
    display: block;
    width: 100%;
    margin-bottom: 24px;
`

const Description = styled.div`
    font-family: "Mouse";
    font-size: 12px;
    display: block;
    width: 100%;

    p {
        margin-bottom: 24px;
    }
`

const Button = styled.button`
    background: ${props => props.theme.limit === props.theme.state ? 'red' : 'green'};
`

const descriptionGenerator = (info, page) => {
    let output = []
    info[page].description.forEach((value, index, array) => {
        output.push(<p key={index}>{value}</p>)
    })
    return output
}

export default function MainHelp(props) {
    const [page, setPage] = useState(0)
    const data = useContext(GameContext);
    const info = data.gameData.helpInfo


    return (
        <Wrapper>
            <Title>HELP</Title>
            <SubTitle>{info[page].title}</SubTitle>
            <Description>{descriptionGenerator(info, page)}</Description>
            <Controls>
                <Button
                    theme={
                        {
                            state: page,
                            limit: 0
                        }
                    }
                    name="less"
                    onClick={
                        () => {
                            let pageHandler = page
                            setPage(page === 0 ? 0 : --pageHandler)
                        }
                    }>{'<'}</Button>
                <Button
                    theme={
                        {
                            state: page,
                            limit: info.length - 1
                        }
                    }
                    name="more"
                    onClick={
                        () => {
                            let pageHandler = page
                            setPage(pageHandler === info.length - 1 ? info.length - 1 : ++pageHandler)
                        }
                    }>{'>'}</Button>
            </Controls>
        </Wrapper >
    )
}
