import React, { useContext } from 'react';
import styled from 'styled-components'
import GameContext from './../game-context'

import MainPlay from './left-columns/MainPlay.js'
import MainOptions from './left-columns/MainOptions.js'
import MainLSX from './left-columns/MainLSX.js'
import MainHelp from './left-columns/MainHelp.js'
import MainCredits from './left-columns/MainCredits.js'
import PlayResults from './left-columns/PlayResults.js'
import PlayRent from './left-columns/PlayRent.js'
import PlayUpgrades from './left-columns/PlayUpgrades.js'
import PlayStaff from './left-columns/PlayStaff.js'
import PlayMarketing from './left-columns/PlayMarketing.js'
import PlayRecipe from './left-columns/PlayRecipe.js'
import PlaySupplies from './left-columns/PlaySupplies.js'

import { greenLeftColumn } from './../styles/variables.js'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 8px 8px 0px;
    background: ${greenLeftColumn};
    border-radius: 8px;
    box-shadow: inset 3px 3px rgba(0,0,0,0.6);

    &>div {
        margin-bottom: 12px;
    }
`

export default function LeftColumn(props) {

    const context = useContext(GameContext).state;

    if (context.gameLayout === 'Main Menu') {
        return (
            <Wrapper>
                {(
                    context.topLeftContainer === 'play!' ? <MainPlay handleChangeState={props.handleChangeState} /> :
                        context.topLeftContainer === 'options' ? <MainOptions handleChangeState={props.handleChangeState} /> :
                            context.topLeftContainer === 'LSX' ? <MainLSX handleChangeState={props.handleChangeState} /> :
                                context.topLeftContainer === 'help' ? <MainHelp handleChangeState={props.handleChangeState} /> :
                                    context.topLeftContainer === 'credits' ? <MainCredits handleChangeState={props.handleChangeState} /> :
                                        'not valid at LeftColumns'
                )}
            </Wrapper>
        )
    } else if (context.gameLayout === 'Prepare Menu') {
        return (
            <Wrapper>
                {(
                    context.topLeftContainer === 'results' ? <PlayResults handleChangeState={props.handleChangeState} /> :
                        context.topLeftContainer === 'rent' ? <PlayRent handleChangeState={props.handleChangeState} /> :
                            context.topLeftContainer === 'upgrades' ? <PlayUpgrades handleChangeState={props.handleChangeState} /> :
                                context.topLeftContainer === 'staff' ? <PlayStaff handleChangeState={props.handleChangeState} /> :
                                    context.topLeftContainer === 'marketing' ? <PlayMarketing handleChangeState={props.handleChangeState} /> :
                                        context.topLeftContainer === 'recipe' ? <PlayRecipe handleChangeState={props.handleChangeState} /> :
                                            context.topLeftContainer === 'supplies' ? <PlaySupplies handleChangeState={props.handleChangeState} /> :
                                                context.topLeftContainer + ' is not valid'
                )}
            </Wrapper>
        )
    } else {
        return 'not valid at LeftColumns'
    }
}
