
export default class GamesAvaliable {
    constructor() {
        this.mountedGame = false
        this.gamesAvaliable = []
    }

    pushGamesInSession(games) {
        for (const [key, value] of Object.entries(games)) {
            new Promise((resolve, reject) => {
                try {
                    this.gamesAvaliable[key] = value
                } catch (error) {
                    reject(error)
                }
                resolve()
            })
        }
    }

    processGame(Game) {
        if (!Game) {
            alert('Select a game please')
            console.log('Select a game please')
        } else {
            this.changeState('Play State', Game);
        }
    }
}
