import React, { useContext } from 'react';
import styled from 'styled-components'
import GameContext from './../../../game-context'
import { greenRightColumn } from './../../../styles/variables.js'
import { yesterday, charts, profitAndLost, balance } from './../../../styles/icons.js'

const Wrapper = styled.div`
    display: inline-flex;
    justify-content: flex-end;
    position: relative;
    z-index: 2;
    top: 3px;
    margin-right: 8px;
`

const Button = styled.div`
    padding: 4px 0;    
    cursor: pointer;
    background: ${props => props.theme.active ? greenRightColumn : 'rgba(255,255,255,0)'};
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 35px;
    margin-right: 6px;
    border-radius: 5px 5px 0 0;
    transition: background 0.2s ease, box-shadow 0.2s ease, border 0.2s ease;
    border-right: 2px solid rgba(255,255,255,0);
    border-left: 2px solid rgba(255,255,255,0);
    border-bottom: 2px solid rgba(255,255,255,0);
    box-shadow: inset 0px 2px 0 0 rgba(0,0,0,0);
    
    ${props => props.theme.active ? window.styled.css`
        border-right: 2px solid rgba(255,255,255,0.2);
        box-shadow: inset 0px 2px 0 0 rgba(0,0,0,0.6);
        border-left: 2px solid rgba(0,0,0,0.6);
        ` : `
        &:hover {
            border-right: 2px solid rgba(255,255,255,0.1);
            box-shadow: inset 0px 2px 0 0 rgba(0,0,0,0.1);
            border-left: 2px solid rgba(0,0,0,0.1);
        }`
    }
`

export default function SquareTopControls(props) {
    const context = useContext(GameContext).state.topLeftContainer;

    //TODO: create a generator for this buttons
    if (context === `results`) {
        return (
            <Wrapper>
                <Button
                    theme={{ active: context.LeftColumn === `Yesterday's Results` }}
                    onClick={() => props.handleChangeState({ LeftColumn: `Yesterday's Results` })}>
                    <img src={yesterday} alt="yesterday's results" width="26px" />
                </Button>
                <Button
                    theme={{ active: context.LeftColumn === `Charts` }}
                    onClick={() => props.handleChangeState({ LeftColumn: `Charts` })}>
                    <img src={charts} alt="charts" width="24px" />
                </Button>
                <Button
                    theme={{ active: context.LeftColumn === `Profit & Lost` }}
                    onClick={() => props.handleChangeState({ LeftColumn: `Profit & Lost` })}>
                    <img src={profitAndLost} alt="profit and lost" width="29px" />
                </Button>
                <Button
                    theme={{ active: context.LeftColumn === `Balance Sheet` }}
                    onClick={() => props.handleChangeState({ LeftColumn: `Balance Sheet` })}>
                    <img src={balance} alt="balance" width="29px" />
                </Button>
            </Wrapper>
        )
    } else if (context === `supplies`) {
        return (
            <Wrapper>
                <Button onClick={() => props.handleChangeState({ LeftColumn: `Yesterday's Results` })}>
                    <img src="" alt="" />
                </Button>
                <Button onClick={() => props.handleChangeState({ LeftColumn: `Charts` })}>
                    <img src="" alt="" />
                </Button>
                <Button onClick={() => props.handleChangeState({ LeftColumn: `Profit & Lost` })}>
                    <img src="" alt="" />
                </Button>
                <Button onClick={() => props.handleChangeState({ LeftColumn: `Balance Sheet` })}>
                    <img src="" alt="" />
                </Button>
            </Wrapper>
        )
    } else {
        return context.LeftColumn + ' Not valid to SquareTop Controls'
    }
}