import React, { useContext } from 'react';
import styled from 'styled-components'

import GameContext from './../../game-context'

import { greenRightColumn } from './../../styles/variables.js'
import SquareTopControls from './modules/SquareTopControls.js'
import YesterdaysResults from './modules/YesterdaysResults.js'
import Charts from './modules/Charts.js'
import ProfitAndLost from './modules/ProfitAndLost.js'
import BalanceSheet from './modules/BalanceSheet.js'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 8px 4px 4px;
    position: relative;
`

const Title = styled.span`
    font-family: "LoRes22";
    font-size: 22px;
    display: block;
    width: 100%;
    margin-bottom: 8px;
`

const Information = styled.div`
    background: ${greenRightColumn};
    border-radius: 8px;
    border-right: 2px solid rgba(255,255,255,0.2);
    box-shadow: inset 2px 2px 0 0 rgba(0,0,0,0.6);
    position: relative;
    z-index: 1;
    padding: 6px 8px;

`

export default function PlayResults(props) {
    const context = useContext(GameContext).state;

    return (
        <Wrapper>
            <Title>{context.LeftColumn}</Title>
            <SquareTopControls handleChangeState={props.handleChangeState}/>
            <Information>
                {(
                    context.LeftColumn === `Yesterday's Results` ? <YesterdaysResults /> :
                        context.LeftColumn === `Charts` ? <Charts /> :
                            context.LeftColumn === `Profit & Lost` ? <ProfitAndLost /> :
                                context.LeftColumn === `Balance Sheet` ? <BalanceSheet /> :
                                    context.LeftColumn + ' is not valid in Play Results'

                )}
            </Information>
        </Wrapper>
    )
}